import React from 'react';
import { Button, Modal, Form } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {
  
  var old = props.datos.datos;

  function editCard(){

    // Datos metidos en el formulario:
    var id = parseInt(document.querySelector('#formIdCard').value);
    var partner_name = document.querySelector('#formPartnerName').value;
    var partner_phone = document.querySelector('#formPartnerPhone').value;
    var partner_address = document.querySelector('#formPartnerAdress').value;
    var partner_pic = document.querySelector('#formPartnerPic').value;
    var partner_documentation = document.querySelector('#formPartnerDoc').value;
    var partner_card = document.querySelector('#formPartnerCard').value;
    var monitor = document.querySelector('#formMonitor').value;
    var moth_paid = document.querySelector('#formMothPaid').value;
    var estage = document.querySelector('#formPartnerEstage').value;

    // Para saber quien edita:
    var userName = sessionStorage.getItem('userName');
    var timeStamp = Date.now();
    var created_by = old.created_by ? old.created_by : 'valkiria';

    var updates;
    if(old.updates){
      updates = old.updates;
      updates.push({
        'edited_by': userName,
        'edited_date': timeStamp
      });
    }else{
      updates = [{
        'edited_by': userName,
        'edited_date': timeStamp
      }];
    }    

    db.collection("ags_cards").where("id", "==", old.id)
    .get()
    .then(querySnapshot => {
      querySnapshot.forEach(function(doc) {        
        db.collection("ags_cards").doc(doc.id).set({
          'id': id,
          'partner_name': partner_name,
          'partner_phone': partner_phone,
          'partner_address': partner_address,
          'partner_pic': partner_pic,
          'partner_documentation': partner_documentation,
          'partner_card': partner_card,
          'monitor': monitor,
          'moth_paid': moth_paid,
          'estage': estage,
          'created_by': created_by,   
          'updates': updates
        })
        .then(function() {
            console.log("Document successfully written!");
            window.location.reload(false);
        })
        .catch(function(error) {
            console.error("Error writing document: ", error);
        });
      })
    })
    .catch(function(error) {
        console.log("Error getting documents: ", error);
    });
  }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Crear Tarjeta American Gym Style
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group controlId="formIdCard">
            <Form.Label>Nº de Tarjeta:</Form.Label>
            <Form.Control type="text" required defaultValue={old.id} />
          </Form.Group>
          <Form.Group controlId="formPartnerName">
            <Form.Label>Nombre del Partner:</Form.Label>
            <Form.Control type="text" required defaultValue={old.partner_name} />
          </Form.Group>
          <Form.Group controlId="formPartnerPhone">
            <Form.Label>Teléfono del Partner:</Form.Label>
            <Form.Control type="text" defaultValue={old.partner_phone} />
          </Form.Group>
          <Form.Group controlId="formPartnerAdress">
            <Form.Label>Dirección del Partner:</Form.Label>
            <Form.Control type="text" defaultValue={old.partner_address}/>
          </Form.Group>
          <Form.Group controlId="formPartnerPic">
            <Form.Label>Imagen de la foto del Partner:</Form.Label>
            <Form.Control type="text" defaultValue={old.partner_pic}/>
          </Form.Group>
          <Form.Group controlId="formPartnerDoc">
            <Form.Label>Imagen de la documentación del Partner:</Form.Label>
            <Form.Control type="text" defaultValue={old.partner_documentation}/>
          </Form.Group>
          <Form.Group controlId="formPartnerCard">
            <Form.Label>Imagen de la tarjeta del Partner:</Form.Label>
            <Form.Control type="text" defaultValue={old.partner_card}/>
          </Form.Group>
          <Form.Group controlId="formMonitor">
            <Form.Label>Monitor asignado:</Form.Label>
            <Form.Control type="text" defaultValue={old.monitor}/>
          </Form.Group>
          <Form.Group controlId="formMothPaid">
            <Form.Label>Mes Pagado:</Form.Label>
            <Form.Control as="select" defaultValue={old.moth_paid}>
              <option value="0_No">No</option>
              <option value="1_Sí">Si</option>           
            </Form.Control>
          </Form.Group>
          <Form.Group controlId="formPartnerEstage">
            <Form.Label>Estado:</Form.Label>
            <Form.Control as="select" defaultValue={old.estage}>
              <option value="0_Activado">Activado</option>
              <option value="1_Desactivado">Desactivado</option>
              <option value="3_Archivado">Archivado</option>
            </Form.Control>
          </Form.Group>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="dark" onClick={() => editCard(props)} onClickCapture={props.onHide}>Guardar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Editcardmodal(datostabla) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="light" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-pencil-square" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456l-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
          <path fillRule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        datos={datostabla}
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Editcardmodal;