import React from 'react';
import { Button, Modal, Form } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {
  
  var old = props.datos.datos;

  function Editobmodal(){
    // Datos metidos en el formulario:
    var name = document.querySelector('#formNameJob').value;
    var coordinator = document.querySelector('#formCoordinatorJob').value;
    var principal_image = document.querySelector('#formPrinImgJob').value;
    var description = document.querySelector('#formDescJob').value;
    var status = document.querySelector('#formStatusJob').value;
    var priority = document.querySelector('#formPriorityJob').value;
    var media = (document.querySelector('#formMediaJob').value).split(',');

    // Para saber quien edita:
    var userName = sessionStorage.getItem('userName');
    var timeStamp = Date.now();
    var created_by = old.created_by ? old.created_by : 'valkiria';

    var updates;
    if(old.updates){
      updates = old.updates;
      updates.push({
        'edited_by': userName,
        'edited_date': timeStamp
      });
    }else{
      updates = [{
        'edited_by': userName,
        'edited_date': timeStamp
      }];
    }

    db.collection("jobs").where("name", "==", old.name)
    .get()
    .then(querySnapshot => {
      querySnapshot.forEach(function(doc) {
        
        db.collection("jobs").doc(doc.id).set({
          'name': name,
          'coordinator': coordinator,
          'principal_image': principal_image,
          'description': description,
          'status': status,
          'priority': priority,
          'media': media,
          'created_by': created_by,
          'updates': updates
          // 'edited_by': userName,
          // 'last_updated': timeStamp,          
        })
        .then(function() {
          console.log("Document successfully written!");
          window.location.reload(false);
        })
        .catch(function(error) {
          console.error("Error writing document: ", error);
        });
      })
    })
    .catch(function(error) {
        console.log("Error getting documents: ", error);
    });
  }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Editar Operación
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
        <Form.Group controlId="formNameJob">
            <Form.Label>Nombre</Form.Label>
            <Form.Control type="text" defaultValue={old.name} />
          </Form.Group>
          <Form.Group controlId="formCoordinatorJob">
            <Form.Label>Coordinador/a</Form.Label>
            <Form.Control type="text" defaultValue={old.coordinator} />
          </Form.Group>
          <Form.Group controlId="formPrinImgJob">
            <Form.Label>Imagen Principal</Form.Label>
            <Form.Control type="text" defaultValue={old.principal_image} />
          </Form.Group> 
          <Form.Group controlId="formDescJob">
            <Form.Label>Descripción:</Form.Label>
            <Form.Control  as="textarea" rows={3} defaultValue={old.description} />
          </Form.Group>
          <Form.Group controlId="formMediaJob">
            <Form.Label>Imágenes</Form.Label>
            <Form.Control type="text" defaultValue={old.media} />
          </Form.Group>
          <Form.Group controlId="formStatusJob">
            <Form.Label>Estado:</Form.Label>
            <Form.Control as="select" defaultValue={old.status}>
              <option value="2_En Proceso">En Proceso</option>
              <option value="4_Finalizado">Finalizado</option>
              <option value="6_Archivado">Archivado</option>  
            </Form.Control>
          </Form.Group> 
          <Form.Group controlId="formPriorityJob">
            <Form.Label>Prioridad:</Form.Label>
            <Form.Control as="select" defaultValue={old.priority}>
              <option value="2_Alta">Alta</option>
              <option value="4_Media">Media</option>
              <option value="6_Baja">Baja</option>    
            </Form.Control>
          </Form.Group> 
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="dark" onClick={() => Editobmodal()} onClickCapture={props.onHide}>Guardar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Editobmodal(datostabla) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="light" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-pencil-square" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456l-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
          <path fillRule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        datos={datostabla}
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Editobmodal;