import React from 'react';
import { Button, Modal, Image } from 'react-bootstrap';

function MyVerticallyCenteredModal(props) {

  var data = props.datos.datos;

  var userName = sessionStorage.getItem('userName');

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {data.id} - {data.partner_name}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="text-center mb-1">
          {data.partner_card ? <Image src={data.partner_card} width="100%"></Image> : <strong>No hay foto.</strong>}
        </div>     
        <p>
          <b>Nombre del Partner:</b><br></br>
          <div className="ml-3">{data.partner_name ? data.partner_name : 'No hay información.'}</div>       
        </p>
        <p>
          <b>Teléfono del Partner:</b>
          <div className="ml-3">{data.partner_phone ? data.partner_phone : 'No hay información.'}</div>  
        </p>
        <p>
          <b>Dirección del Partner:</b>
          <div className="ml-3">{data.partner_address ? data.partner_address: 'No hay descripción.'}</div>  
        </p>
        <p>
          <b>Monitor Asignado:</b>
          <div className="ml-3">{data.monitor ? data.monitor: 'No hay descripción.'}</div>  
        </p>
        <p>
          <b>Mes Pagado:</b>
          <div className="ml-3">{data.moth_paid ? data.moth_paid.substr(2, data.moth_paid.lenght): 'No hay descripción.'}</div>  
        </p>
        <p>
          <b>Estado:</b>
          <div className="ml-3">{data.estage ? data.estage.substr(2, data.estage.lenght): 'No hay descripción.'}</div>  
        </p>
        <div className="text-center mb-1">
          {data.partner_pic ? <Image src={data.partner_pic} height="200px"></Image> : <strong>No hay foto.</strong>}
        </div>  
        <div className="text-center mb-1">
          {data.partner_documentation ? <Image src={data.partner_documentation} height="300px"></Image> : <strong>No hay foto.</strong>}
        </div>  

        { userName === 'valkiria' ? 
          <div>
            <p><b>Creado por:</b><div className="ml-3">{data.created_by ? data.created_by : ''}</div></p>
            {/* <p><b>Editado por:</b><div className="ml-3">{data.edited_by ? data.edited_by : ''}</div></p>
            <p><b>Última actualización:</b><div className="ml-3">{fecha_ultedit}</div></p> */}
          </div>
          : ''
        }
        { userName === 'valkiria' ?
            <p><b>Actualizaciones:</b>
              <div>
                { userName === 'valkiria' ? 
                  data.updates ? data.updates.map((listValue, index) => {
                    
                    var fucEdit = new Date(listValue['edited_date']);
                    var fecha_ultedit = fucEdit.getDate()+'-'+(fucEdit.getMonth()+1)+'-'+fucEdit.getFullYear()+'  '+fucEdit.getHours()+':'+(fucEdit.getMinutes());
                    return(
                      <div>
                        <b className="mx-2">{listValue['edited_by']}</b>  {fecha_ultedit}
                      </div>
                    )
                  }): '' : ''
                }
              </div>
            </p>
          : ''
        }
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Infocabmodal(props) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="light" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-eye-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path d="M10.5 8a2.5 2.5 0 1 1-5 0 2.5 2.5 0 0 1 5 0z"/>
          <path fillRule="evenodd" d="M0 8s3-5.5 8-5.5S16 8 16 8s-3 5.5-8 5.5S0 8 0 8zm8 3.5a3.5 3.5 0 1 0 0-7 3.5 3.5 0 0 0 0 7z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        datos={props}
      />
    </>
  );
}

export default Infocabmodal;