                                <p className="text-right m-2">
                                    <Creategumodal datos={datostablaGuns}></Creategumodal>
                                </p>
                                <p>                                
                                    <Button variant="success" className="mx-1" disabled>
                                        Cortas <Badge variant="light">{nCortas}</Badge>
                                    </Button>

