import React from 'react';
import {Button, Modal, Form } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {
  var userName = sessionStorage.getItem('userName');

  function createJob(){    
    var name = document.querySelector('#formNameJob').value;
    var coordinator = document.querySelector('#formCoordinatorJob').value;
    var principal_image = document.querySelector('#formPrinImgJob').value;
    var description = document.querySelector('#formDescJob').value;
    var status = document.querySelector('#formStatusJob').value;
    var priority = document.querySelector('#formPriorityJob').value;
    var media = (document.querySelector('#formMediaJob').value).split(',');

    // Para saber quien crea:
    var userName = sessionStorage.getItem('userName');
    var timeStamp = Date.now();

    var updates = [{
      'edited_by': userName,
      'edited_date': timeStamp
    }];

    db.collection("jobs").doc().set({
      'name': name,
      'coordinator': coordinator,
      'principal_image': principal_image,
      'description': description,
      'status': status,
      'priority': priority,
      'media': media,
      'created_by': userName,
      'updates': updates
    })
    .then(function() {
        console.log("Document successfully written!");
        window.location.reload(false);
    })
    .catch(function(error) {
        console.error("Error writing document: ", error);
    });    
  }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Crear Operación
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group controlId="formNameJob">
            <Form.Label>Nombre</Form.Label>
            <Form.Control type="text" placeholder="Introduce el nombre de la Operación." />
          </Form.Group>
          <Form.Group controlId="formCoordinatorJob">
            <Form.Label>Coordinador/a</Form.Label>
            <Form.Control type="text" defaultValue={userName} placeholder="Introduce el nombre de la persona que cooridna la Operación." />
          </Form.Group>
          <Form.Group controlId="formPrinImgJob">
            <Form.Label>Imagen Principal</Form.Label>
            <Form.Control type="text" placeholder="Coloca aquí la url de la imagen principal de la Operación." />
          </Form.Group> 
          <Form.Group controlId="formDescJob">
            <Form.Label>Descripción:</Form.Label>
            <Form.Control  as="textarea" rows={3} required placeholder="Coloca aquí la descripción general de Operación." />
          </Form.Group>
          <Form.Group controlId="formMediaJob">
            <Form.Label>Imágenes</Form.Label>
            <Form.Control type="text" placeholder="Coloca aquí las urls de las imágenes SEPARADAS POR UNA COMA." />
          </Form.Group>
          <Form.Group controlId="formStatusJob">
            <Form.Label>Estado:</Form.Label>
            <Form.Control as="select" defaultValue="2_En Proceso">
              <option value="2_En Proceso">En Proceso</option>
              <option value="4_Finalizado">Finalizado</option>
              <option value="6_Archivado">Archivado</option>  
            </Form.Control>
          </Form.Group> 
          <Form.Group controlId="formPriorityJob">
            <Form.Label>Prioridad:</Form.Label>
            <Form.Control as="select" defaultValue="1_Alta">
              <option value="2_Alta">Alta</option>
              <option value="4_Media">Media</option>
              <option value="6_Baja">Baja</option>    
            </Form.Control>
          </Form.Group> 
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="dark" onClick={() => createJob(props)} onClickCapture={props.onHide}>Guardar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Createjobmodal() {
  const [modalShow, setModalShow] = React.useState(false);
  return (
    <>
      <Button variant="dark" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="3em" height="3em" viewBox="0 0 16 16" className="bi bi-plus-circle-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path fillRule="evenodd" d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4.5a.5.5 0 0 0-1 0v3h-3a.5.5 0 0 0 0 1h3v3a.5.5 0 0 0 1 0v-3h3a.5.5 0 0 0 0-1h-3v-3z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Createjobmodal;